const Header = () => {
    const [open, setOpen] = useState(false);
    const [isSticky, setIsSticky] = useState(false);
    const location = useLocation();
    const isLogin = location?.state?.isLogin

    // nav-mobile
    const handleClickOpen = () => {
        setOpen(true);
      };
    
      const handleClose = () => {
        setOpen(false);
      };
    // 

    useEffect(() => {
    }, [isSticky]);
   
    return (
        <>
            <header id="header" className={isSticky ? "sticky" : ""}>
                <div className="navb">
                    {/* <div className="overlay" /> */}
                    <img onClick={()=>handleClickOpen()} className="btn-mobile" alt="btn" src={btnMobi} ></img>
                    <div className="menu-mobile">
                        <div className="navbar-navb-mobile-btn"> 
                            <Dialog
                                className='custom-menu'
                                open={open}
                                TransitionComponent={Transition}
                                keepMounted
                                onClose={handleClose}
                                aria-describedby="alert-dialog-slide-description"
                                fullWidth={true}
                                maxWidth="lg"
                                PaperProps={{
                                style: {
                                    height: '100%',
                                    width: '100%',
                                    maxWidth:'100%',
                                    margin: '0px',
                                    position: 'absolute',
                                    top: '0px',
                                    overflowY: 'visible',
                                }
                                }}
                            >
                                <DialogContent>
                                    <div>
                                        <div className="menu-top">
                                            <FontAwesomeIcon className="" icon={faUserCircle} fontSize={50} color="#fff"/>
                                            <div className="">
                                                <h3 className="text-1 text-font-1-3em">Ti khon</h3>
                                                <a className="text-1 text-font-14" href='/login'>ng nhp</a>
                                            </div>
                                        </div>
                                        <div className="list-menu-mobile">
                                            <ul>
                                                {navs.map((item, index)=>(
                                                    <>
                                                        {item.submenu ? (
                                                            <>
                                                                <li className="text-font-1em">
                                                                    <Link to={item.id} style={{color:'#000000'}} >
                                                                        {item.value} <ExpandMoreIcon sx={{ fontSize: 20 }} />
                                                                    </Link>
                                                                </li>
                                                            </>
                                                        ):(

                                                            <li className="text-font-1em">
                                                                <Link to={item.id} style={{color:'#000000'}} >
                                                                    {item.value}
                                                                </Link>
                                                            </li>
                                                        )}
                                                    </>

                                                ))}
                                            </ul>
                                        </div>
                                    </div>
                                    
                                </DialogContent>
                            </Dialog>
                        </div>
                    </div>
                    <a to="/" className="navbar-title">
                        <img src={logo}></img>               
                    </a>
                    <nav className="nav_actions">
                        <Navbar />
                    </nav>
                    <div className="user-dropdown">
                        <UserDropdown />
                    </div>
                    {/* {isLogin || JSON.parse(localStorage.getItem("userDataUser"))?.token ?
                        <UserDropdown />
                        : <Link to="/login">
                            <div className="btn">Login</div>
                        </Link>
                    }  */}
                </div>
            </header>
        </>
    );
};

export default memo(Header);
